<template>
	<div>
		<div class="back-title"><span>我发布的</span></div>
		<div class="pagebox">
			<Menu mode="horizontal" active-name="1" @on-select="turnUrl" class="item-title-box">
		        <MenuItem name="/article/own">
		            	全部文章
		        </MenuItem>
		        <MenuItem name="/article/published">
		            	已发表
		        </MenuItem>
		        <MenuItem name="/article/Artilceownflow">
		            	未通过
		        </MenuItem>
		    </Menu>
		    <router-view></router-view>
		</div>
    </div>
</template>
<script>
 export default {
   methods: {
     turnUrl (name) {
       this.$router.push(name)
     }
   }
 }
</script>


